import * as React from "react"
import { Link } from "gatsby"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header

  if (isRootPath) {
    header = (
      <h1 className="main-heading">
        <Link to="/">{title}</Link>
      </h1>
    )
  } else {
    header = (
      <Link className="header-link-home" to="/">
        {title}
      </Link>
    )
  }

  return (
    <div className="global-wrapper" data-is-root-path={isRootPath}>
      <header className="global-header">{header}</header>
      <main>{children}</main>
      <footer>
        <p>
          <b>{new Date().getFullYear()}</b>, Built with{" "}
          <a href="https://www.gatsbyjs.com">Gatsby</a>
          {` `}
          {/*
           * Příklad zobrazení licence na vašem blogu.
           * Pro zobrazení licence je třeba vložit její text nebo odkaz na webovou stránku,
           * na které je licence definována.
           * V tomto případě používáme odkaz na licenci CC BY-NC-ND 4.0.
           */}
          site license:{" "}
          <a
            href="http://creativecommons.org/licenses/by-nc-nd/4.0/"
            target="_blank"
            rel="license noopener noreferrer"
            style={{ display: "inline-block" }}
          >
            CC BY-NC-ND 4.0
          </a>
        </p>
      </footer>
    </div>
  )
}

export default Layout
